"use client";

import Link from "next/link";
import { useEffect, useState } from "react";
import { Button } from "~/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "~/components/ui/card";
import { Users, Plus } from "lucide-react";
import type { User, Game } from "@prisma/client";

type TeamWithRelations = {
    id: number;
    name: string;
    code: string;
    gameId: number | null;
    currentPuzzleId: number | null;
    game: Pick<Game, "id" | "name"> | null;
    users: Pick<User, "id" | "email">[];
};

export default function TeamsPage() {
    const [teams, setTeams] = useState<TeamWithRelations[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        async function fetchTeams() {
            try {
                const response = await fetch('/api/teams');
                if (!response.ok) throw new Error('Failed to fetch teams');
                const data = (await response.json()) as { teams: TeamWithRelations[] };
                setTeams(data.teams);
            } catch (error) {
                console.error('Error fetching teams:', error);
            } finally {
                setLoading(false);
            }
        }

        void fetchTeams();
    }, []);

    if (loading) {
        return <div>Loading teams...</div>;
    }

    return (
        <div>
            <div className="mb-8 flex items-center justify-between">
                <div>
                    <h1 className="text-3xl font-bold tracking-tight">Teams</h1>
                    <p className="text-slate-400">Manage your game teams.</p>
                </div>
                <Button asChild>
                    <Link href="/admin/teams/new">
                        <Plus className="mr-2 h-4 w-4" />
                        New Team
                    </Link>
                </Button>
            </div>

            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
                {teams.map((team) => (
                    <Card key={team.id} className="border-slate-800 bg-slate-900/50">
                        <CardHeader className="flex flex-row items-center justify-between pb-2">
                            <CardTitle className="text-lg font-medium">
                                {team.name}
                            </CardTitle>
                            <Users className="h-4 w-4 text-slate-400" />
                        </CardHeader>
                        <CardContent>
                            <div className="space-y-4">
                                <div>
                                    <div className="text-sm text-slate-400">Game</div>
                                    <div>{team.game?.name ?? "Not joined any game"}</div>
                                </div>
                                <div>
                                    <div className="text-sm text-slate-400">Team Code</div>
                                    <div className="font-mono">{team.code}</div>
                                </div>
                                <div>
                                    <div className="text-sm text-slate-400">Players</div>
                                    <div className="space-y-1">
                                        {team.users.map((user) => (
                                            <div key={user.id} className="text-sm">
                                                {user.email}
                                            </div>
                                        ))}
                                    </div>
                                </div>
                                <div className="flex justify-end gap-2">
                                    <Button
                                        variant="outline"
                                        size="sm"
                                        asChild
                                    >
                                        <Link href={`/admin/teams/${team.id}`}>
                                            Edit
                                        </Link>
                                    </Button>
                                    <Button
                                        variant="destructive"
                                        size="sm"
                                        onClick={async () => {
                                            try {
                                                const response = await fetch(`/api/teams/${team.id}`, {
                                                    method: 'DELETE',
                                                });
                                                if (!response.ok) throw new Error('Failed to delete team');
                                                setTeams(teams.filter(t => t.id !== team.id));
                                            } catch (error) {
                                                console.error('Error deleting team:', error);
                                            }
                                        }}
                                    >
                                        Delete
                                    </Button>
                                </div>
                            </div>
                        </CardContent>
                    </Card>
                ))}
            </div>
        </div>
    );
} 